export default {
	props:['selectImags'],
	data() {
		return {
			title:''
		}
	},
	methods: {
		closeImg(index){
			this.$emit('closeImg',index)
		},
		addPhoto(){
			this.$emit('addPhoto')
		},
		addPrize(item,index){
			let val = prompt('请输入该产品的价格数字')
			item.prize = val
		},
		onKeyInput(event){
			this.title = event.target.value
		},
		uploadForm(){
			if(!this.title){
				alert('请输入活动标题')
				return
			}
			let data = {
				title: this.title,
				files: this.selectImags
			}
			console.log(data)
		}
	}
}
